<template>
    <div class="savedPage">
        <h1>Saved Character Stat Blocks</h1>
        <div class='savedStats' v-for="block in blocks" :key="block.id"> 
            <div class='savedStatBlock'>
            
                <div class='blockUpper'>
                    <div class='savedStat'>
                        <h1>{{block.str}}</h1>
                        <h2>STR</h2>
                    </div>
                    <div class='savedStat'>
                        <h1>{{block.dex}}</h1>
                        <h2>DEX</h2>
                    </div>
                    <div class='savedStat'>
                        <h1>{{block.con}}</h1>
                        <h2>CON</h2>
                    </div>
                    <div class='savedStat'>
                        <h1>{{block.int}}</h1>
                        <h2>INT</h2>
                    </div>
                    <div class='savedStat'>
                        <h1>{{block.wis}}</h1>
                        <h2>WIS</h2>
                    </div>
                    <div class='savedStat'>
                        <h1>{{block.cha}}</h1>
                        <h2>CHA</h2>
                    </div>
                    <button class='button editStat' v-on:click="editBlock(block)">Edit</button>
                    <button class='button deleteStat' v-on:click="deleteBlock(block)">Delete</button>
                    </div>
                <div class='blockLower'>
                    <textarea class='editTextBox' v-model="editText" v-if="editing === block._id" placeholder="Add description"></textarea>
                    <p v-if="block.desc && !editing"><em>{{block.desc}}</em></p>
                    <button class='button saveStat' v-if="editing === block._id" v-on:click="saveDesc(block)">Save</button>
                </div>
                
            </div>
            
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: "SavedList",
    data() {
        return {
            blocks: [],
            editing: '',
            editText: '',
        }
    },
    created() {
        this.getBlocks();
    },
    methods: {
        async getBlocks() {
            try {
                let response = await axios.get('/api/stats');
                this.blocks = response.data;
                return true;
            } catch (error) {
                console.log(error);
            }
        },
        async deleteBlock(block) {
            try {
                await axios.delete('/api/stats/' + block._id);
                this.getBlocks();
                return true;
            } catch (error) {
                console.log(error);
            }
        },
        editBlock(block) {
            this.editing = block._id;
        },
        async saveDesc(block) {
            try {
                await axios.put('/api/stats/' + block._id, {
                    desc: this.editText
                });
                this.getBlocks();
                this.editing = '';
                this.editText = '';
                return true;
            } catch (error) {
                console.log(error);
            }
        }
    },
}
</script>

<style scoped>
.savedStats{
    margin-top: 100px;
}
.savedStatBlock{
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    background-color:rgb(32, 69, 109);
}
.blockUpper {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.savedStat{
    padding: 20px;
}
.deleteStat {
    padding: 20px;
    margin-right: 30px;
}
.savedStat h2 {
    color: azure;
}
.saveStat {
    padding: 10px;
    margin-bottom: 30px;
}
.blockLower{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.blockLower p {
    margin-bottom: 30px;
}
.editTextBox {
    margin-bottom: 30px;
}
</style>